"use client";

import React, { useState, useRef, useEffect } from "react";
import { motion, AnimatePresence } from "framer-motion";
import { projects } from "@/data/projects";
import Image from "next/image";
import Link from "next/link";
import { ArrowLeft, ArrowRight, ExternalLink } from "lucide-react";

export default function ThreeDCarousel() {
    const [activeIndex, setActiveIndex] = useState(0);
    const [rotation, setRotation] = useState(0);
    const [isAutoSpin, setIsAutoSpin] = useState(true);

    const projectCount = projects.length;
    const rotateStep = 360 / projectCount;
    const radius = 600;

    const handleNext = () => {
        setIsAutoSpin(false);
        const newRotation = rotation - rotateStep;
        setRotation(newRotation);
        // Calculate the positive index for indicators
        setActiveIndex((prev) => (prev + 1) % projectCount);
    };

    const handlePrev = () => {
        setIsAutoSpin(false);
        const newRotation = rotation + rotateStep;
        setRotation(newRotation);
        setActiveIndex((prev) => (prev - 1 + projectCount) % projectCount);
    };

    // Auto-spin logic - maintains same direction
    useEffect(() => {
        if (!isAutoSpin) return;
        const interval = setInterval(() => {
            setRotation((prev) => prev - rotateStep);
            setActiveIndex((prev) => (prev + 1) % projectCount);
        }, 8000);
        return () => clearInterval(interval);
    }, [isAutoSpin, rotateStep, projectCount]);

    return (
        <motion.div
            initial={{ opacity: 0, y: 300, rotateX: 45, rotateY: -720 }}
            whileInView={{ opacity: 1, y: 0, rotateX: 0, rotateY: 0 }}
            viewport={{ once: true, margin: "-100px" }}
            transition={{
                duration: 2.5,
                ease: [0.16, 1, 0.3, 1]
            }}
            className="relative w-full h-[700px] flex items-center justify-center perspective overflow-visible select-none"
        >
            {/* --- The Spatial Stage --- */}
            <div className="relative w-full h-full flex items-center justify-center p-20 preserve-3d">

                {/* 3D Rotor Wrapper */}
                <motion.div
                    className="relative w-full h-full preserve-3d"
                    animate={{ rotateY: rotation }}
                    transition={{ type: "spring", stiffness: 40, damping: 20, mass: 1 }}
                >
                    {projects.map((project, index) => {
                        const itemRotation = index * rotateStep;
                        // Determine if this item is currently facing front
                        // We use index matching but based on the rotation modulo
                        const currentFacingIndex = (Math.round(-rotation / rotateStep) % projectCount + projectCount) % projectCount;
                        const isActive = currentFacingIndex === index;

                        return (
                            <motion.div
                                key={project.id}
                                className="absolute top-1/2 left-1/2 -mt-[250px] -ml-[400px] w-[800px] h-[500px] preserve-3d"
                                style={{
                                    transform: `rotateY(${itemRotation}deg) translateZ(${radius}px)`,
                                    backfaceVisibility: "hidden"
                                }}
                            >
                                {/* The "Card" Structure */}
                                <motion.div
                                    className={`relative w-full h-full rounded-[3rem] overflow-hidden border transition-all duration-[1500ms] ${isActive ? 'border-cyan-400/50 shadow-[0_0_120px_rgba(34,211,238,0.25)] scale-100 opacity-100' : 'border-white/5 opacity-40 scale-90 grayscale shadow-2xl'}`}
                                >
                                    {/* Authentic Screenshot Render */}
                                    <Image
                                        src={project.image}
                                        alt={project.title}
                                        fill
                                        className="object-cover object-top hover:scale-110 transition-transform duration-[8000ms]"
                                        priority={index === 0}
                                    />

                                    {/* Cinematic Overlay */}
                                    <div className={`absolute inset-0 bg-gradient-to-t from-black via-black/10 to-transparent transition-opacity duration-1000 ${isActive ? 'opacity-80' : 'opacity-40'}`} />

                                    {/* Top-Level Tags (Architectural Badge) */}
                                    <div className={`absolute top-10 left-10 right-10 flex justify-between items-start transition-all duration-1000 ${isActive ? 'translate-y-0 opacity-100' : '-translate-y-5 opacity-0'}`}>
                                        <div className="flex flex-col gap-2">
                                            <span className="px-5 py-2 rounded-full bg-cyan-400/20 backdrop-blur-xl border border-cyan-400/30 text-[10px] font-black uppercase tracking-[0.4em] text-cyan-400 w-fit shadow-[0_0_20px_rgba(34,211,238,0.2)]">
                                                Protocol {index + 1}
                                            </span>
                                            <div className="flex items-center gap-2">
                                                <div className="w-1.5 h-1.5 rounded-full bg-cyan-400 animate-pulse outline outline-4 outline-cyan-400/20" />
                                                <span className="text-[9px] font-bold text-white/40 uppercase tracking-[0.2em]">{project.tags[0]} Infrastructure</span>
                                            </div>
                                        </div>
                                        <div className="px-5 py-2.5 rounded-xl bg-black/60 backdrop-blur-md border border-white/5 text-[10px] font-black text-white/40 uppercase tracking-[0.3em] leading-none">
                                            {project.year}
                                        </div>
                                    </div>

                                    {/* Action Footer */}
                                    <div className={`absolute bottom-12 left-12 right-12 transition-all duration-1000 ${isActive ? 'translate-y-0 opacity-100' : 'translate-y-10 opacity-0'}`}>
                                        <div className="flex items-center gap-6">
                                            <Link
                                                href={`/portfolio/${project.id}`}
                                                className="px-10 py-5 bg-white text-black rounded-full font-black uppercase tracking-[0.2em] text-[10px] hover:bg-cyan-400 hover:text-white transition-all shadow-2xl shadow-cyan-400/20 active:scale-95"
                                            >
                                                Initialize Build
                                            </Link>
                                            <a
                                                href={project.netlifyUrl}
                                                target="_blank"
                                                rel="noopener noreferrer"
                                                className="flex items-center gap-3 text-[10px] font-black uppercase tracking-[0.3em] text-white/30 hover:text-cyan-400 transition-colors group/link pb-1 border-b border-transparent hover:border-cyan-400/50"
                                            >
                                                Live Intel <ExternalLink className="w-4 h-4 group-hover/link:translate-x-1 group-hover/link:-translate-y-1 transition-transform" />
                                            </a>
                                        </div>
                                    </div>
                                </motion.div>
                            </motion.div>
                        );
                    })}
                </motion.div>
            </div>

            {/* --- Radial Navigation Control --- */}
            <div className="absolute -bottom-16 left-1/2 -translate-x-1/2 flex items-center gap-16 z-50">
                <button
                    onClick={handlePrev}
                    className="group w-20 h-20 rounded-full border border-white/10 flex items-center justify-center hover:border-cyan-400/50 hover:bg-cyan-400/5 transition-all active:scale-90"
                >
                    <ArrowLeft className="w-8 h-8 group-hover:-translate-x-2 transition-transform" />
                </button>

                <div className="flex items-center gap-6">
                    {projects.map((_, i) => (
                        <button
                            key={i}
                            onClick={() => {
                                setIsAutoSpin(false);
                                // Find shortest path for manual navigation
                                let diff = i - activeIndex;
                                if (diff > projectCount / 2) diff -= projectCount;
                                if (diff < -projectCount / 2) diff += projectCount;
                                setRotation(rotation - diff * rotateStep);
                                setActiveIndex(i);
                            }}
                            className={`h-1.5 rounded-full transition-all duration-1000 ${activeIndex === i ? 'w-24 bg-cyan-400 shadow-[0_0_25px_rgba(34,211,238,0.6)]' : 'w-6 bg-white/10 hover:bg-white/40'}`}
                        />
                    ))}
                </div>

                <button
                    onClick={handleNext}
                    className="group w-20 h-20 rounded-full border border-white/10 flex items-center justify-center hover:border-cyan-400/50 hover:bg-cyan-400/5 transition-all active:scale-90"
                >
                    <ArrowRight className="w-8 h-8 group-hover:translate-x-2 transition-transform" />
                </button>
            </div>

            {/* Global Perspective Grid Overlay */}
            <div className="absolute inset-0 pointer-events-none -z-20 opacity-25">
                <div className="absolute bottom-0 w-full h-[400px] bg-gradient-to-t from-cyan-400/10 to-transparent blur-[120px]" />
                <div className="absolute top-1/2 left-1/2 -translate-x-1/2 -translate-y-1/2 w-[1400px] h-[1400px] border border-cyan-400/5 rounded-full" />
            </div>
        </motion.div>
    );
}
